html {
    height: 100%;
    box-sizing: border-box;
    margin: 0;
}

body {
    height: 100%;
    width: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(33, 33, 148, 0.4)), url(../images/bg1.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0;
}

.container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 20px
}

.search-wrap {
    display: flex;
    justify-content: center;
}

.btn, .search-value {
    margin: 10px 30px;
}

input.search-value {
    padding: 15px;
    max-width: 350px;
    width: 100%;
    border: 1px solid rgb(245 206 98);
    border-radius: 3px;
    box-shadow: 0 4px 15px 0 rgb(228 54 3 / 75%);
}
input.search-value::placeholder {
    color: #2c230a;
    font-style: italic;
    font-size: 15px;
    opacity: 0.5;
}

input.search-value:focus {
    outline: none;
    box-shadow: 0 5px 10px 10px rgb(228 54 3 / 75%);
    moz-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}


.result-wrap {
    transform: translateY(30%);
}

.country-search-result {
    color: white;
    max-width: 600px;
    width: 100%;
    margin: 0 auto;
    transform: translateY(30%);
}

.country-list-item {
    font-size: 35px;
}

.full-country-info {
    color: white;
    font-size: 25px;
    font-weight: 700;
}

.country-name {
    text-align: center;
    text-transform: uppercase;
}

.country-info-wrap {
    display: flex;
    align-items: space-between;
    justify-content: space-evenly;
}

.country-info-items {
    padding: 10px 0;
}

.country-flag img{
    max-width: 300px;
    width: 100%;
}

